:root {
  /* Radius tiers */
  --radius-sm: 6px;
  --radius-md: 10px;
  --radius-lg: 14px;
  --radius: 10px;

  /* Core theme â€” teal primary + warm stone neutrals */
  --background: 30 6% 98%;
  --foreground: 24 10% 10%;
  --card: 0 0% 100%;
  --card-foreground: 24 10% 10%;
  --popover: 0 0% 100%;
  --popover-foreground: 24 10% 10%;
  --primary: 173 58% 39%;
  --primary-foreground: 0 0% 100%;
  --secondary: 30 6% 96%;
  --secondary-foreground: 24 10% 10%;
  --muted: 30 6% 96%;
  --muted-foreground: 24 5% 45%;
  --accent: 30 6% 96%;
  --accent-foreground: 24 10% 10%;
  --destructive: 0 72% 51%;
  --destructive-foreground: 0 0% 100%;
  --border: 24 6% 90%;
  --input: 24 6% 90%;
  --ring: 173 58% 39%;

  /* Teal scale */
  --teal-50: #F0FDFA;
  --teal-100: #CCFBF1;
  --teal-200: #99F6E4;
  --teal-300: #5EEAD4;
  --teal-400: #2DD4BF;
  --teal-500: #14B8A6;
  --teal-600: #0D9488;
  --teal-700: #0F766E;
  --teal-800: #115E59;
  --teal-900: #134E4A;

  /* Stone scale */
  --stone-50: #FAFAF9;
  --stone-100: #F5F5F4;
  --stone-200: #E7E5E4;
  --stone-300: #D6D3D1;
  --stone-400: #A8A29E;
  --stone-500: #78716C;
  --stone-600: #57534E;
  --stone-700: #44403C;
  --stone-800: #292524;
  --stone-900: #1C1917;

  /* Semantic */
  --success: #16A34A;
  --success-bg: #F0FDF4;
  --warning: #D97706;
  --warning-bg: #FFFBEB;
  --error: #DC2626;
  --error-bg: #FEF2F2;
  --info: #0D9488;
